// axios
import theAxios from 'axios'

// import router from '@/router/index.js'
// import { Notify } from 'vant'
import { removeToken } from '@/utils/token.js'
import { getNewTokenAPI } from '@/api/api.js'
import { Notify } from 'vant'
import router from '@/router/index.js'

const axios = theAxios.create({
  baseURL: 'http://geek.itheima.net'
})
// 
axios.interceptors.request.use(function(config) {
  // 
  // : token
  // token, api/index.jsAuthorization, 
  // undefined, null
  // ?. , length, undefined
  // getToken()token, length
  // if (getToken()?.length > 0 && config.headers.Authorization === undefined) {
  if (window.localStorage.getItem('itheima')?.length > 0 && config.headers.Authorization === undefined) {
    // config.headers.Authorization = `Bearer ${getToken()}`
    config.headers.Authorization = `Bearer ${window.localStorage.getItem('itheima')}`
  }
  return config
}, function(error) {
  // 
  return Promise.reject(error)
})

// 
// : 
axios.interceptors.response.use(function(response) {
  // http2xx, 3xx
  // 
  return response
}, async function(error) {
  // http4xx, 5xx
  // 
  // console.dir(error)
  // console.log(this) // undefined
  // 401, 
  if (error.response.status === 401) {
    // this.$router (thisvue$router)
    // : this.$routerrouter, @/routerrouter
    // Notify({ type: 'warning', message: '' })

    removeToken() // token, , 
    // 1: , 
    // router.currentRoute  vuethis.$route -> 
    // fullPath, #
    // 1tokentoketoken
    // router.replace(`/login?path=${router.currentRoute.fullPath}`)

    // 2refresh_tokentoken, JS, ()
    const res = await getNewTokenAPI()
    console.log(res)
    // token
    // 1 token
    localStorage.setItem('token', res.data.data.token)
    // setToken(res.data.data.token)
    // 2 token
    error.config.headers.Authorization = `Bearer ${res.data.data.token}`
    // 3 
    // error.configreturn
    return axios(error.config)
    // tokenrefresh_token
  } else if (error.response.status === 500 && error.config.url === '/v1_0/authorization' && error.config.method === 'put') {
    // tokentoken
    // token
    localStorage.clear()
    Notify({ type: 'warning', message: '' })
    router.replace('/login')
  }

  // 2: refresh_tokentoken, JS, ()
  //   const res = await getNewTokenAPI()

  //   // token, 
  //   // 1. token
  //   setToken(res.data.data.token)
  //   // 2. token
  //   error.config.headers.Authorization = `Bearer ${res.data.data.token}`
  //   // 3. , 
  //   // error.config
  //   // return-returnPromise
  //   return axios(error.config)
  // } else if (error.response.status === 500 && error.config.url === '/v1_0/authorizations' && error.config.method === 'put') {
  //   // refresh_token
  //   localStorage.clear() // localStorage
  //   // localStorage, , localStorage
  //   Notify({ type: 'warning', message: '' })
  //   router.replace('/login')
  // }

  return Promise.reject(error)
})
// params = {}, data = {}, headers = {}
export default ({ url, method = 'GET', params = {}, data = {}, headers = {} }) => {
  return axios({
    url,
    method,
    params,
    data,
    headers
  })
}
