// 1. axios, datanullparamsnull)
// 2art_idart_id,art_idnull
export const sendCommentAPI = ({ id, content, art_id = null }) => {
  const obj = {
    target: id,
    content
  }
  if (art_id !== null) {
    // art_id
    obj.art_id = art_id
  }
  return request({
    url: '/v1_0/comments',
    method: 'POST',
    // headers: {
    //   Authorization: `Bearer ${localStorage.getItem('token')}`
    // },
    data: obj
  })
}
