export const updateUserPicAPI = (fileObj) => request({
  url: '/v1_0/user/photo',
  method: 'PATCH',
  // headers: {
  //   // Content-TypeaxiosContent-Type
  //   Authorization: `Bearer ${localStorage.getItem('token')}`
  // },
  data: fileObj
})
// 
export const updateUserProfileAPI = (dataObj) => {
  const obj = {
    name: '',
    gender: '',
    birthday: '',
    real_name: '',
    intro: ''
  }
  for (const i in obj) {
    if (dataObj[i] === undefined) {
      delete obj[i]
    } else {
      obj[i] = dataObj[i]
    }
  }
  // undefined

  return request({
    url: '/v1_0/user/profile',
    method: 'PATCH', // PATCH
    // headers: {
    //   Authorization: `Bearer ${localStorage.getItem('token')}`
    // },
    data: obj
  })
}

